.baner {
  --v-baner-title-padding-x: 0.6667rem;
  --v-baner-padding: 2rem;
  --v-baner-wrapper-gap: 1.3333rem;
  padding: var(--v-baner-padding);
  background: center center/cover no-repeat;
}
.baner__wrapper {
  display: flex;
  align-items: center;
  gap: var(--v-baner-wrapper-gap);
  flex-wrap: wrap;
}
.baner__img {
  min-width: 190px;
}
.baner__title {
  flex-basis: 275px;
  flex-grow: 1;
  border-left: none;
  padding: 2rem;
  line-height: 110%;
}
@media screen and (min-width: 1200px) {
  .baner__title {
    padding-top: var(--v-baner-title-padding-x);
    padding-bottom: var(--v-baner-title-padding-x);
    flex-basis: 370px;
  }
}
@media screen and (min-width: 576px) {
  .baner__title {
    padding-left: 2rem;
    border-left: 1px solid #4d4d4d;
  }
}
.baner__text {
  flex-basis: 470px;
  flex-grow: 1;
  font-size: 0.8667rem;
  line-height: 130%;
  color: var(--v-secondary);
  padding: 0;
}